
#contact {
    padding-top: 50px;
    padding-bottom: 70px;
    background-color: #F0F0F0;
}

.email-icon {
    width: 100px;
    height: 100px;
    line-height: 100px;
    max-width: 100%;
    font-size: 55px;
    text-align: center;
    vertical-align: middle;
    position: relative;
    margin: 0 auto 10px;
}

#contact p {
    font-size: 16px;
    font-weight: 300;
    max-width: 450px;
    margin: 0 auto;
}

#contact #email-button {
    color: #000;
    padding: 10px 20px;
    text-decoration: none;
    font-family: 'Lato', sans-serif;
    font-size: 20px;
    /*border-radius: 30px;*/
    border: 2px solid #000;
    -webkit-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
}

#contact #email-button:hover {
    color: #fff;
    background: #0a192f;
    border: 2px solid #0a192f;
}

@media screen and (max-width: 480px) {
    #contact {
        padding: 15px 10px 50px;
    }
}


