@import url(https://fonts.googleapis.com/css?family=Sacramento&display=swap);

.logo{
    color: white;
}

.active {
    color: aqua !important;
  }

#intro {
    background-image:url(/static/media/Badal-Sherpa-Avenger.3bdfd90f.gif);
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-color: #000;
    color: #000;
}

#intro .overlay {
    /* FF3.6+ */
    /* Chrome,Safari4+ */
    /* Chrome10+,Safari5.1+ */
    /* Opera 11.10+ */
    /* IE10+ */
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0.73) 17%, rgba(0, 0, 0, 0.66) 35%, rgba(0, 0, 0, 0.55) 62%, rgba(0, 0, 0, 0.4) 100%);
    /* W3C */
    filter: progid: DXImageTransform.Microsoft.gradient(startColorstr='#cc000000', endColorstr='#66000000', GradientType=0);
    /* IE6-9 */
    height: 750px;
    background-attachment: fixed;
}

#intro h1 {
    color: #FFF;
    font-size: 60px;
    font-weight: 300;
    letter-spacing: 3px;
}

#intro p.subtitle {
    color: #ddd;
    font-size: 1.3rem;
}

.intro-content {
    position: absolute;
    padding: 20% 0 5% 0;
    left: 0;
    right: 0;
    z-index: 0;
}

h1 {
  font-size: calc(20px + 18vh);
  line-height: calc(20px + 20vh);
  text-shadow: 0 0 5px #ffa500, 0 0 15px #ffa500, 0 0 20px #ffa500, 0 0 40px #ffa500, 0 0 60px #ff0000, 0 0 10px #ff8d00, 0 0 98px #ff0000; color: #fff6a9;
  font-family: "Sacramento", cursive;
  /* font-family: Arvo, cursive; */
  text-align: center;
  animation: blink 12s ;
  -webkit-animation: blink 12s ;
  font-size: 5px;
}



@-webkit-keyframes blink {
  20%,
  24%,
  55% {
    color: #111;
    text-shadow: none;
  }

  0%,
  19%,
  21%,
  23%,
  25%,
  54%,
  56%,
  100% {
  text-shadow: 0 0 5px #ffa500, 0 0 15px #ffa500, 0 0 20px #ffa500, 0 0 40px #ffa500, 0 0 60px #ff0000, 0 0 10px #ff8d00, 0 0 98px #ff0000;
    color: #fff6a9;
  }
}

@keyframes blink {
  20%,
  24%,
  55% {
    color: #111;
    text-shadow: none;
  }

  0%,
  19%,
  21%,
  23%,
  25%,
  54%,
  56%,
  100% {

  text-shadow: 0 0 5px #ffa500, 0 0 15px #ffa500, 0 0 20px #ffa500, 0 0 40px #ffa500, 0 0 60px #ff0000, 0 0 10px #ff8d00, 0 0 98px #ff0000;
    color: #fff6a9;
  }
}
@media screen and (max-width: 1000px) {
    .intro-content {
        padding: 25% 0 5% 0;
    }
}

@media screen and (max-width: 768px) {
    .intro-content {
        padding: 30% 0 5% 0;
    }
    .navbar-toggle {
        padding: 0px 10px;
    }
}

@media screen and (max-width: 580px) {
    .intro-content {
        padding: 45% 0 5% 0;
    }
    i.fa.fa-angle-down {
        margin-top: 50px !important;
    }
}

i.fa.fa-angle-down {
    padding: 10px 14px;
    color: #aaa;
    font-size: 50px;
    margin-top: 50px;
    transition: all 0.5s;
}

@media screen and (min-width: 1600px) {
    i.fa.fa-angle-down {
        position: relative;
        bottom: 50px;
    }
}

i.fa.fa-angle-down:hover {
    color: #fff;
    text-decoration: none;
}

i.fa.fa-angle-down:focus {
    text-decoration: none;
}




/* ======= ABOUT ======= */
#about {
    padding-top: 100px;
    padding-bottom: 100px;
    background-color: white;
}

#about img#prof-pic {
    width: 200px;
    height: auto;
}

#about p {
    font-size: 16px;
    max-width: 650px;
    margin: 30px auto;
}

#about a {
    font-weight: 500;
}

#about img {
    border-radius: 50%;
}

#about #tl-dr {
    background: #f0f0f0;
    color: #555;
    text-align: center;
    margin: 0px auto;
    padding: 20px;
    height: 200px;
    width: 50%;
    max-width: 650px;
}

#about #tl-dr h3 {
    font-weight: 300;
    font-size: 22px;
    margin-bottom: 40px;
    color: #111;
}

#about #tl-dr h4 {
    font-weight: 300;
    font-size: 17px;
    margin-bottom: 20px;
    text-transform: capitalize;
}

#about #tl-dr h5 {
    font-weight: 300;
    font-size: 14px;
    font-family: 'Roboto Slab', sans-serif;
    text-transform: capitalize;
    letter-spacing: 0.5px;
}

.tldr-proc {
    width: 30%;
    display: inline-block;
    vertical-align: top;
}

.tldr-icon {
    width: 50px;
    height: 50px;
    line-height: 50px;
    font-size: 36px;
    margin: 0 auto 10px;
}

#tldr-keyboard {
    font-size: 50px;
}

@media screen and (max-width: 1100px) {
    #about #tl-dr {
        width: 70%;
    }
}

@media screen and (max-width: 990px) {
    #about #tl-dr h3 {
        font-size: 18px;
    }
}

@media screen and (max-width: 770px) {
    #about #tl-dr h3 {
        font-size: 16px;
    }
    #about #tl-dr h4 {
        font-size: 14px;
    }
    #about #tl-dr h5 {
        font-size: 12px;
    }
}

@media screen and (max-width: 600px) {
    #about #tl-dr {
        padding: 30px 0;
    }
    #about #tl-dr h5 {
        font-size: 11px;
    }
}

@media screen and (max-width: 500px) {
    #about {
        padding: 60px 10px;
    }
    #about #tl-dr h5 {
        /*font-size: 10px;*/
    }
    .tldr-icon {
        font-size: 30px;
    }
    #tldr-keyboard {
        font-size: 40px;
    }
}

@media screen and (max-width: 480px) {
    #about img#prof-pic {
        width: 150px;
    }
    #about #tl-dr {
        width: 90%;
    }
    h2 {
        font-size: 22px;
    }
    .tlder-proc {
        width: 32%;
    }
}

@media screen and (max-width: 360px) {
    #about #tl-dr {
        padding: 20px 0;
        height: 180px;
        width: 95%;
    }
    #about #tl-dr h4 {
        margin-bottom: 5px;
    }
    .tldr-icon {
        /*font-size: 24px;*/
    }
    #tldr-keyboard {
        font-size: 34px;
    }
}
